/*! destyle.css v3.0.0 | MIT License | https://github.com/nicolas-cusan/destyle.css */

*,
::before,
::after {
  box-sizing: border-box;
  border-style: solid;
  border-width: 0;
}

html {
  line-height: 1.15;
  -webkit-text-size-adjust: 100%;
  -webkit-tap-highlight-color: transparent;
}

body {
  margin: 0;
}

main {
  display: block;
}

p,
table,
blockquote,
address,
pre,
iframe,
form,
figure,
dl {
  margin: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: inherit;
  font-weight: inherit;
  margin: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

dt {
  font-weight: bold;
}

dd {
  margin-left: 0;
}

hr {
  box-sizing: content-box;
  height: 0;
  overflow: visible;
  border-top-width: 1px;
  margin: 0;
  clear: both;
  color: inherit;
}

pre {
  font-family: monospace,monospace;
  font-size: inherit;
}

address {
  font-style: inherit;
}

a {
  background-color: transparent;
  text-decoration: none;
  color: inherit;
}

abbr[title] {
  -webkit-text-decoration: underline dotted;
  text-decoration: underline dotted;
}

b,
strong {
  font-weight: bolder;
}

code,
kbd,
samp {
  font-family: monospace,monospace;
  font-size: inherit;
}

small {
  font-size: 80%;
}

sub,
sup {
  font-size: 75%;
  line-height: 0;
  position: relative;
  vertical-align: baseline;
}

sub {
  bottom: -0.25em;
}

sup {
  top: -0.5em;
}

svg,
img,
embed,
object,
iframe {
  vertical-align: bottom;
}

button,
input,
optgroup,
select,
textarea {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  vertical-align: middle;
  color: inherit;
  font: inherit;
  background: transparent;
  padding: 0;
  margin: 0;
  border-radius: 0;
  text-align: inherit;
  text-transform: inherit;
}

[type=checkbox] {
  -webkit-appearance: checkbox;
  -moz-appearance: checkbox;
  appearance: checkbox;
}

[type=radio] {
  -webkit-appearance: radio;
  -moz-appearance: radio;
  appearance: radio;
}

button,
[type=button],
[type=reset],
[type=submit] {
  cursor: pointer;
}

button:disabled,
[type=button]:disabled,
[type=reset]:disabled,
[type=submit]:disabled {
  cursor: default;
}

:-moz-focusring {
  outline: auto;
}

select:disabled {
  opacity: inherit;
}

option {
  padding: 0;
}

fieldset {
  margin: 0;
  padding: 0;
  min-width: 0;
}

legend {
  padding: 0;
}

progress {
  vertical-align: baseline;
}

textarea {
  overflow: auto;
}

[type=number]::-webkit-inner-spin-button,
[type=number]::-webkit-outer-spin-button {
  height: auto;
}

[type=search] {
  outline-offset: -2px;
}

[type=search]::-webkit-search-decoration {
  -webkit-appearance: none;
}

::-webkit-file-upload-button {
  -webkit-appearance: button;
  font: inherit;
}

label[for] {
  cursor: pointer;
}

details {
  display: block;
}

summary {
  display: list-item;
}

[contenteditable]:focus {
  outline: auto;
}

table {
  border-color: inherit;
}

caption {
  text-align: left;
}

td,
th {
  vertical-align: top;
  padding: 0;
}

th {
  text-align: left;
  font-weight: bold;
}

.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  overflow: hidden;
  clip: rect(0 0 0 0);
  -webkit-clip-path: inset(50%);
  clip-path: inset(50%);
  border: 0;
}

body {
  background-color: #e3f0f0;
  margin: 0;
  font-family: "Noto Sans JP",sans-serif;
  color: #000;
}

body.ua-safari {
  -webkit-font-smoothing: antialiased;
}

.common-container {
  background-image: url("../images/page-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
}

input[type=checkbox] {
  opacity: 0;
  position: absolute;
}

img,
source {
  max-width: 100%;
  height: auto;
}

.about .main {
  background-color: gray;
}

.footer {
  text-align: center;
  padding: 1em 0;
}

.footer__text {
  color: #fff;
  text-shadow: 2px 2px 4px rgba(51,51,51,.5);
  font-size: 14px;
}

.header {
  background-color: #f0ed44;
  color: #000;
  box-shadow: 0 4px 4px -4px rgba(26,60,77,.4);
}

.header__inner {
  padding: 1em;
  display: flex;
  align-items: center;
  justify-self: flex-start;
}

.header__img {
  flex: 0 0 auto;
  width: 10%;
  max-width: 80px;
}

.header__title {
  font-size: 22px;
  letter-spacing: .1em;
  flex: 1 0 auto;
  margin-left: 10px;
}

.main {
  margin: 30px 10px;
  padding: 20px 10px 40px;
  border-radius: 8px;
  position: relative;
  z-index: 0;
  box-shadow: 0 4px 4px -4px rgba(26,60,77,.4);
}

.main::before {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: -1;
  background-color: #fff;
  border-radius: 8px;
  opacity: .8;
}

.main__section+.main__section {
  margin-top: 40px;
}

.about__title {
  font-size: 20px;
  line-height: 1.7;
  font-weight: 700;
}

.about__desc {
  font-size: 14px;
  margin-top: 20px;
  line-height: 1.6;
  text-align: justify;
  text-justify: inter-ideograph;
  text-justify: inter-character;
}

.about__img {
  margin-top: 40px;
}

.sample__title {
  text-align: center;
  background-color: #333;
  color: #fff;
  padding: .3em 0;
  font-size: 16px;
  line-height: 1.5;
}

.food__title {
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  margin-top: 20px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.food__desc {
  margin-top: 20px;
  font-size: 14px;
  line-height: 1.6;
}

.food__inner {
  margin-top: 20px;
}

.form label {
  width: 100%;
  height: 100%;
  background: #fff;
  cursor: pointer;
}

.form label.is-clicked {
  outline: 4px solid #eb3434;
  outline-offset: -4px;
}

.main__link {
  margin-top: 40px;
  font-size: 12px;
  line-height: 1.5;
}

.main__link a {
  display: block;
}

.main__link img {
  vertical-align: middle;
  margin-left: .5em;
}

.card-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-top: -10px;
}

.card-container::after {
  content: "";
  width: calc((100% - 10px)/3);
  display: block;
}

.card {
  display: flex;
  flex-direction: column;
  border-radius: 4px;
  width: calc((100% - 10px)/3);
  box-shadow: 0 4px 4px -4px rgba(26,60,77,.4);
  overflow: hidden;
  margin-top: 10px;
}

.card__imag img {
  width: 100%;
  height: 100%;
  -o-object-fit: contain;
  object-fit: contain;
}

.card__title {
  font-size: 20px;
}

.food__result {
  margin-top: 20px;
  padding: 1em;
  position: -webkit-sticky;
  position: sticky;
  bottom: 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  width: 100%;
  background-color: #f0ed44;
  box-shadow: 0 4px 6px 0px rgba(37,37,37,.5);
  outline: 2px solid #000;
  outline-offset: -4px;
}

.result__total {
  font-size: 16px;
  width: 100%;
  text-align: left;
  display: inline-block;
  white-space: nowrap;
}

.result__sum {
  display: inline-block;
  padding: 0 .5em;
}

.result__select {
  margin-top: 1em;
  text-align: left;
  align-self: flex-start;
  font-size: 14px;
}

.result__list {
  display: block;
  min-height: 1em;
  line-height: 1;
}

.u-bold {
  font-weight: 700 !important;
}

.u-underline {
  background: linear-gradient(transparent 70%, #f0ed44 0%) !important;
  display: inline !important;
}

.u-pc {
  display: none;
}

.no-webp .top-fv__title.title-sp {
  background-image: url(../images/fv-title-sp.png);
}

.webp .top-fv__title.title-sp {
  background-image: url(../images/webp/fv-title-sp.webp);
}

@media (min-width: 960px) {
  .footer__text {
    font-size: 16px;
  }

  .header__title {
    margin-left: 20px;
    font-size: 30px;
  }

  .main {
    margin: 40px 20px;
    padding: 40px;
    max-width: 1000px;
    margin-right: auto;
    margin-left: auto;
  }

  .main__section+.main__section {
    margin-top: 80px;
  }

  .about__title {
    font-size: 28px;
  }

  .about__desc {
    font-size: 20px;
    line-height: 1.8;
  }

  .about__img {
    margin-top: 60px;
  }

  .sample {
    max-width: 500px;
    margin-right: auto;
    margin-left: auto;
  }

  .sample__title {
    font-size: 20px;
  }

  .food__title {
    margin-top: 80px;
    font-size: 24px;
  }

  .food__title::before,
  .food__title::after {
    border-top: 1px solid;
    content: "";
    width: 5em;
  }

  .food__title::before {
    margin-right: 1em;
  }

  .food__title::after {
    margin-left: 1em;
  }

  .food__desc {
    font-size: 20px;
  }

  .food__inner {
    margin-top: 40px;
  }

  .main__link {
    font-size: 16px;
  }

  .main__link a {
    display: inline-block;
    transition: opacity .2s ease-out;
  }

  .main__link a:hover {
    opacity: .5;
  }

  .card-container {
    margin-top: -20px;
    width: 80%;
    margin-right: auto;
    margin-left: auto;
  }

  .card-container::after {
    width: calc((100% - 40px)/3);
  }

  .card {
    width: calc((100% - 40px)/3);
    margin-top: 20px;
  }

  .food__result {
    margin-top: 40px;
    padding: 1em;
    outline: 2px solid #000;
    outline-offset: -6px;
  }

  .result__total {
    font-size: 18px;
  }

  .result__select {
    font-size: 16px;
  }

  .result__list {
    line-height: 1.2;
    min-height: 1.5em;
  }

  .u-pc {
    display: block;
  }
}